* {
  padding: 0;
  margin: 0;
}

.form {
  display: block;

  &-field {
    display: block;
    box-sizing: border-box;
    width: 480px;
    background: #191c26;
    padding: 84px 31px;
  }

  &-input {
    display: block;
    width: 100%;
    height: 24px;
    background: #191c26;
    margin-top: 44px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 11px;
    line-height: 15px;
    /* identical to box height */

    letter-spacing: 0.1em;
    text-transform: uppercase;

    color: rgba(157, 164, 189, 0.4);

    outline: none;
    border: none;
    border-bottom: 1px solid rgba(157, 164, 189, 0.4);

    &:hover,
    &:focus,
    &:not(:placeholder-shown) {
      color: #ffffff;
    }
  }

  &-button {
    display: block;
    width: 160px;
    height: 40px;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #ff6c00;
    box-sizing: border-box;
    border-radius: 25px;
    margin-top: 44px;
    background: #191c26;
  }
}

.message {
  width: 100%;
  height: 76px;
  resize: none;
  background: #191c26;
  margin-top: 44px;
  border: none;
  outline: none;
  border-bottom: 1px solid rgba(157, 164, 189, 0.4);
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  /* identical to box height */

  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: rgba(157, 164, 189, 0.4);
}

.title {
  font-family: Raleway;
  font-style: normal;
  font-weight: bold;
  font-size: 42px;
  line-height: 49px;
  text-align: center;
  letter-spacing: 0.05em;
  // margin-top: 53px;

  color: #ffffff;
}

.text {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;

  color: #ffffff;
}

.contacts-container {
  display: block;
  width: 480px;
  height: 496px;
  padding: 84px 31px;
  box-sizing: border-box;
  background-image: linear-gradient(
      to right,
      rgba(25, 28, 38, 0.9),
      rgba(25, 28, 38, 0.9)
    ),
    url(../images/background-image.jpg);
}

.contacts-list {
  list-style: none;
  padding-top: 40px;
  padding-bottom: 44px;
}

.contacts-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}
.contacts-text,
.contacts-link {
  display: inline-block;
  .contacts-text {
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    /* identical to box height, or 167% */

    letter-spacing: 0.02em;

    color: #9da4bd;
  }
}

.contacts-link {
  text-decoration: underline;
}

.contacts-icon {
  margin-right: 16px;
}

.worktime {
  position: relative;
}
.subtitle {
  display: block;
  margin-left: 81px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  /* identical to box height */

  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: #ffffff;

  &::after {
    display: block;
    position: absolute;
    justify-content: center;
    content: '';
    width: 60px;
    height: 1px;
    top: 7px;
    left: 1px;
    background: rgba(157, 164, 189, 0.4);
  }
}

.timing {
  display: block;
  padding-top: 20px;
}
